\r\n          .sorting_1{\r\n            background-color: rgba(241, 244, 251, 0.2) !important;\r\n          }\r\n          &.odd{\r\n            background-color: $white;\r\n          }\r\n        }\r\n      }\r\n      .vendor-list {\r\n        align-items: center;\r\n        img {\r\n          margin-right: 20px;\r\n          height: 40px;\r\n          width: auto !important;\r\n        }\r\n      }\r\n      td {\r\n        padding: 0.75rem !important;\r\n      }\r\n      th{\r\n        background-color: #f1f4fb !important;\r\n        padding: 24px 0.75rem!important;\r\n      }\r\n      thead {\r\n        .sorting, .sorting_asc, .sorting_desc, .sorting_asc_disabled, .sorting_desc_disabled {\r\n          &:before,\r\n          &:after {\r\n            bottom: 25px;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n.category-table {\r\n  table {\r\n    tr {\r\n      td, th {\r\n        text-align: center;\r\n        &:nth-child(2) {\r\n          text-align: left;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n.order-table {\r\n  table {\r\n    tr {\r\n      td, th {\r\n        &:nth-child(3) {\r\n          text-align: center;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n.product-list, .report-table {\r\n  table {\r\n    tr {\r\n      td, th {\r\n        text-align: left !important;\r\n        &:last-child {\r\n          text-align: center !important;\r\n        }\r\n      }\r\n    }\r\n    .jsgrid-filter-row {\r\n      display: none !important;\r\n    }\r\n    .jsgrid-cell {\r\n      padding: 0.75rem;\r\n    }\r\n  }\r\n}\r\n.jsgrid-pager {\r\n  padding: 2em 0 0;\r\n}\r\n.jsgrid-grid-header{\r\n  border:1px solid #e8efff;\r\n}\r\n.user-list {\r\n  img {\r\n    border-radius: 100%;\r\n  }\r\n}\r\n/**=====================\r\n  Admin CSS Ends\r\n==========================**/\r\n\r\n\r\n/**=====================\r\n      Button CSS start\r\n==========================**/\r\n@mixin btn-gradien($deg,$leftcolor,$leftcolor-per,$rightcolor,$rightcolor-per,$font-color){\r\n  background-image: linear-gradient(to right,$leftcolor 0%,$rightcolor 51%,$leftcolor 100%);\r\n  border:none;\r\n  color:$font-color;\r\n  background-size: auto 200%;\r\n  transition: all 0.3s ease;\r\n}\r\n@mixin btn-squre($border-radius){\r\n  border-radius:$border-radius+px;\r\n}\r\n@mixin btn-pill($border-radius){\r\n  border-radius:$border-radius+px;\r\n}\r\n@mixin btn-pill-first($radius){\r\n  border-radius: $radius+px 0 0 $radius+px;\r\n}\r\n@mixin btn-pill-last($radius){\r\n  border-radius: 0 $radius+px $radius+px 0;\r\n}\r\n\r\n.btn{\r\n  font-size: $btn-font-size;\r\n  padding: $btn-padding;\r\n  border-radius: 5px;\r\n  letter-spacing: 1px;\r\n  line-height: 20px;\r\n  text-transform: uppercase;\r\n  font-size: 14px;\r\n  font-weight: 700;\r\n  transition: .3s ease-in-out;\r\n}\r\n.btn-lg{\r\n  font-size: $btn-lg-font-size;\r\n}\r\n.btn-sm{\r\n  font-size: $btn-sm-font-size;\r\n}\r\n.btn-xs {\r\n  padding: $btn-xs-padding;\r\n  font-size: $btn-xs-font-size;\r\n}\r\n.large-btn{\r\n  .btn{\r\n    margin-right: 5px;\r\n    margin-bottom: 15px;\r\n  }\r\n  margin-bottom: -20px;\r\n}\r\n// outline buttons//\r\n@each $btn-name, $btn-color in (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .btn-air-#{$btn-name}{\r\n    box-shadow: 0 5px 10px 2px rgba(88,103,221,.19)!important;\r\n    &:hover,&:active,&:not([disabled]):not(.disabled):active{\r\n      background-color: darken( $btn-color, 10% );\r\n      border-color: darken( $btn-color, 10% );\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// forms //\r\n@each $btn-name, $btn-color in (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .input-air-#{$btn-name}{\r\n    box-shadow: 0 3px 5px 1px rgba(88,103,221,.15)!important;\r\n    &:focus{\r\n      border-color:$btn-color;\r\n    }\r\n  }\r\n}\r\n\r\n//input background //\r\n@each $btn-name, $btn-color in (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .form-bg-#{$btn-name}{\r\n    background:$btn-color;\r\n    border-color:$btn-color;\r\n    @if($btn-name == light){\r\n      color:$dark-color;\r\n    }\r\n    @else{\r\n      color: $white;\r\n    }\r\n    &:focus{\r\n      border-color:$btn-color;\r\n      background:$btn-color;\r\n      @if($btn-name == light){\r\n        color:$dark-color;\r\n      }\r\n      @else{\r\n        color: $white;\r\n      }\r\n      &:active{\r\n        border-color:$btn-color;\r\n        background:$btn-color;\r\n        @if($btn-name == light){\r\n          color:$dark-color;\r\n        }\r\n        @else{\r\n          color: $white;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.btn-warning{\r\n  color: $white;\r\n  &:hover,\r\n  &.disabled{\r\n    color: $white;\r\n  }\r\n}\r\n.btn-primary-gradien{\r\n  @include btn-gradien(60,lighten($primary-color,8%),0%,darken($primary-color,8%),100%,$white);\r\n  &:focus,&:active,&.active{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($primary-color,8%),0%,darken($primary-color,8%),100%,$white) !important;\r\n\r\n  }\r\n}\r\n.btn-secondary-gradien{\r\n  @include btn-gradien(60,lighten($secondary-color,8%),0%,darken($secondary-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($secondary-color,8%),0%,darken($secondary-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n.btn-success-gradien{\r\n  @include btn-gradien(60,lighten($success-color,8%),0%,darken($success-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($success-color,8%),0%,darken($success-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n.btn-danger-gradien{\r\n  @include btn-gradien(60,lighten($danger-color,8%),0%,darken($danger-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($danger-color,8%),0%,darken($danger-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n.btn-warning-gradien{\r\n  @include btn-gradien(60,lighten($warning-color,8%),0%,darken($warning-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($warning-color,8%),0%,darken($warning-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n.btn-info-gradien{\r\n  @include btn-gradien(60,lighten($info-color,8%),0%,darken($info-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($info-color,8%),0%,darken($info-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n.btn-light-gradien{\r\n  @include btn-gradien(60,lighten($light-color,8%),0%,darken($light-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($light-color,8%),0%,darken($light-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n.btn-dark-gradien{\r\n  @include btn-gradien(60,lighten($dark-color,8%),0%,darken($dark-color,8%),100%,$white);\r\n  &:hover,&:focus,&:active,&.active,&.hover{\r\n    transition: 1.5s;\r\n    background-position: right center;\r\n    background-image: linear-gradient(to right, lighten($dark-color,8%),0%,darken($dark-color,8%),100%,$white) !important;\r\n  }\r\n}\r\n[class*='-gradien']{\r\n  &:hover{\r\n    background-size: 50% 100%;\r\n    transition: all 0.3s ease;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n/**=====================\r\n Button CSS end\r\n==========================**/\r\n\r\n/**=====================\r\n      Card CSS Start\r\n==========================**/\r\n\r\n.card{\r\n  margin-bottom: $card-margin-bottom;\r\n  border: $card-border-width;\r\n  transition: all 0.3s ease;\r\n  letter-spacing: 0.5px;\r\n  border-radius: $card-border-radious;\r\n  box-shadow: $card-box-shadow;\r\n  &.card-load {\r\n    .card-loader {\r\n      position: absolute;\r\n      top: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      height: 100%;\r\n      display: flex;\r\n      background-color: rgba(255, 255, 255, 0.7);\r\n      z-index: 8;\r\n      align-items: center;\r\n      justify-content: center;\r\n      i{\r\n        margin: 0 auto;\r\n        color: $primary-color;\r\n        font-size: 20px;\r\n      }\r\n    }\r\n  }\r\n  &.full-card {\r\n    position: fixed;\r\n    top: 80px;\r\n    z-index: 99999;\r\n    box-shadow: none;\r\n    right: 0;\r\n    border-radius: 0;\r\n    border: 1px solid $light-semi-gray;\r\n    width: calc(100vw - 272px);\r\n    height: calc(100vh - 80px);\r\n    .card-body {\r\n      overflow: auto;\r\n    }\r\n  }\r\n  .card-header{\r\n    background-color:  $card-header-bg-color;\r\n    border-bottom: none;\r\n    padding: $card-padding;\r\n    border-bottom: 1px solid $card-border-color;\r\n    border-top-left-radius: $card-border-radious;\r\n    border-top-right-radius: $card-border-radious;\r\n    .card-header-right{\r\n      border-radius: 0 0 0 7px;\r\n      right: 20px;\r\n      top: 25px;\r\n      display: inline-block;\r\n      float: right;\r\n      padding: 7px 0;\r\n      position: absolute;\r\n      .card-option{\r\n        text-align: right;\r\n        width: 35px;\r\n        height: 20px;\r\n        overflow: hidden;\r\n        -webkit-transition: 0.3s ease-in-out;\r\n        transition: 0.3s ease-in-out;\r\n        li{\r\n          display: inline-block;\r\n        }\r\n      }\r\n      i{\r\n        margin: 0 5px;\r\n        cursor: pointer;\r\n        color: $dark-color;\r\n        line-height: 20px;\r\n        &.icofont-refresh{\r\n          font-size: 13px;\r\n        }\r\n      }\r\n    }\r\n    h5{\r\n      font-size:$card-header-font-size;\r\n      margin-bottom: 0;\r\n      text-transform: $card-header-font-transform;\r\n      font-weight: $card-header-font-weight;\r\n      line-height:24px;\r\n    }\r\n    > span{\r\n      font-size: $card-header-span-size;\r\n      color: $card-header-span-color;\r\n      margin-top: 5px;\r\n      display: block;\r\n      letter-spacing: 1px;\r\n    }\r\n  }\r\n  .card-body{\r\n    padding: $card-padding;\r\n    background-color: $card-body-bg-color;\r\n    p{\r\n      &:last-child{\r\n        margin-bottom: 0;\r\n      }\r\n    }\r\n  }\r\n  .sub-title{\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n    padding-bottom: 5px;\r\n    margin-bottom: 8px;\r\n    font-size: 18px;\r\n  }\r\n  .card-footer{\r\n    background-color: $card-footer-bg-color;\r\n    border-top: 1px solid $card-border-color;\r\n    padding: $card-padding;\r\n    border-bottom-left-radius: $card-border-radious;\r\n    border-bottom-right-radius: $card-border-radious;\r\n  }\r\n}\r\n.card-header{\r\n  .nav-material{\r\n    margin-bottom: -13px;\r\n  }\r\n}\r\n/**=====================\r\n     Card CSS End\r\n==========================**/\r\n\r\n/**=====================\r\n      Footer CSS Start\r\n==========================**/\r\nfooter{\r\n  background-color: $footer_bg_color;\r\n  border-top: 1px solid $footer_top_bgr_color;\r\n  padding: 15px;\r\n  bottom: 0;\r\n  left: 0;\r\n  a{\r\n    font-weight: 600;\r\n  }\r\n  &.footer-starter-kit{\r\n    bottom: -52px;\r\n  }\r\n}\r\n.blockquote-footer{\r\n  margin-left: 0 !important;\r\n  width: 885px !important;\r\n}\r\n.page-wrapper{\r\n  .page-body-wrapper{\r\n    footer{\r\n      margin-left: 255px;\r\n      transition: all 0.3s ease;\r\n      bottom: 0;\r\n      z-index: 8;\r\n      p{\r\n        color: rgb(165, 165, 165);\r\n        i{\r\n          color: $danger-color;\r\n          margin-left: 5px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n}\r\n/**=====================\r\n      Footer CSS Ends\r\n==========================**/\r\n\r\n/**=====================\r\n      General CSS Start\r\n==========================**/\r\n//Custom css Start\r\nbody {\r\n  background-color: $body-bg-color;\r\n  font-size: $body-font-size;\r\n  overflow-x: hidden;\r\n  font-family: $font-work-sans, $font-serif;\r\n  color: $theme-body-font-color;\r\n}\r\nul {\r\n  padding-left: $ul-padding-left;\r\n  list-style-type: none;\r\n  margin-bottom: 0;\r\n  padding-right: $ul-padding-right;\r\n}\r\n* a {\r\n  color: $primary-color;\r\n}\r\n*.btn:focus{\r\n  box-shadow : $btn-focus-box-shadow !important;\r\n}\r\np {\r\n  font-size: $paragraph-font-size;\r\n  line-height: $paragraph-line-height;\r\n  letter-spacing: $paragraph-letter-spacing;\r\n}\r\ncode{\r\n  color: $code-tag-color !important;\r\n  background-color: $code-tag-bg-color;\r\n  padding: $code-tag-padding;\r\n  margin: $code-tag-margin;\r\n  border-radius: $code-tag-border-radious;\r\n}\r\nblockquote{\r\n  border-left: $blockquote-border;\r\n  padding: $blockquote-padding;\r\n}\r\nblockquote{\r\n  &.text-center{\r\n    border: none;\r\n    padding: $blockquote-padding;\r\n  }\r\n}\r\nblockquote{\r\n  &.text-right{\r\n    border-left: none;\r\n    border-right: $blockquote-border;\r\n    padding: $blockquote-padding;\r\n  }\r\n}\r\n:focus{\r\n  outline-color: $all-focus-outline-color;\r\n}\r\n.disabled {\r\n  cursor: not-allowed;\r\n  opacity: 0.5;\r\n}\r\nsvg{\r\n  vertical-align: baseline;\r\n}\r\ninput {\r\n  &:focus {\r\n    outline-color: $transparent-color;\r\n  }\r\n}\r\n.txt-dark {\r\n  color: $theme-font-color !important;\r\n}\r\n.txt-success {\r\n  color: $success-color !important;\r\n}\r\n.txt-danger {\r\n  color: $danger-color !important;\r\n}\r\n/**=====================\r\n      General CSS Ends\r\n==========================**/\r\n\r\n$header-size: 80px;\r\n\r\n/**=====================\r\n    Header CSS Start\r\n==========================**/\r\n%common {\r\n  color: $theme-body-font-color;\r\n  border-left-color: $dark-color;\r\n  transition: $sidebar-transition;\r\n}\r\n%for-animated-hover {\r\n  box-shadow: 0 0 2px 2px $light-color;\r\n  transform: translateY(30px);\r\n  opacity: 0;\r\n  visibility: hidden;\r\n  left: 0;\r\n}\r\n%for-animated-hover-box{\r\n  opacity: 1;\r\n  transform: translateY(0px);\r\n  visibility: visible;\r\n  &:before{\r\n    width: 0;\r\n    height: 0;\r\n    border-left: 7px solid transparent;\r\n    border-right: 7px solid transparent;\r\n    border-bottom: 7px solid $white;\r\n    content: \"\";\r\n    top: -7px;\r\n    position: absolute;\r\n    left: 10px;\r\n    z-index: 2;\r\n  }\r\n  &:after{\r\n    width: 0;\r\n    height: 0;\r\n    border-left: 7px solid transparent;\r\n    border-right: 7px solid transparent;\r\n    border-bottom: 7px solid $dark-gray;\r\n    content: \"\";\r\n    top: -7px;\r\n    position: absolute;\r\n    left: 10px;\r\n    z-index: 1;\r\n  }\r\n}\r\n%sidebar-icon {\r\n  display: block;\r\n  transform: rotateX(0deg);\r\n  z-index: 1;\r\n  background-color: $white;\r\n  color: $dark-gray;\r\n  font-size: 14px;\r\n  opacity: 1;\r\n  margin-top: -70px;\r\n  padding: 10px 0;\r\n  text-align: left;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.13);\r\n  position: absolute;\r\n  width: 245px;\r\n  margin-left: 115px;\r\n}\r\n/*======= Page Header css Start ======= */\r\n.page-wrapper{\r\n  .page-main-header{\r\n    background-color: $main-header-bg-color;\r\n    height: $header-size;\r\n    position: $main-header-position;\r\n    top: $main-header-top;\r\n    z-index: $main-header-z-index;\r\n    box-shadow: $main-header-shadow;\r\n    width: calc(100% - #{$sidebar-width});\r\n    display: flex;\r\n    align-items: center;\r\n    margin-left: $sidebar-width;\r\n    transition: $sidebar-transition;\r\n    &.open{\r\n      margin-left: 0;\r\n      width: 100%;\r\n      transition: $sidebar-transition;\r\n    }\r\n  }\r\n  .page-body-wrapper{\r\n    .page-body{\r\n      min-height: calc(100vh - #{$header-size});\r\n      margin-top: $header-size;\r\n      padding: $page-body-padding;\r\n      position: relative;\r\n      background-color: $light-color;\r\n    }\r\n    .page-header{\r\n      padding-top: $page-header-padding;\r\n      padding-bottom: $page-header-padding;\r\n      .row{\r\n        align-items: center;\r\n        .page-header-left {\r\n          display: flex;\r\n          align-items: center;\r\n        }\r\n        h3{\r\n          font-size: $page-title-font-size;\r\n          margin-bottom: $page-title-margin-bottom;\r\n          font-weight: $page-title-font-weight;\r\n          text-transform: $page-title-text-tranform;\r\n          font-family: $font-nunito;\r\n          small {\r\n            display: block;\r\n            font-size: 12px;\r\n            margin-top: 7px;\r\n            letter-spacing: 1px;\r\n            text-transform: capitalize;\r\n            color: $theme-body-sub-title-color;\r\n          }\r\n        }\r\n      }\r\n      .breadcrumb{\r\n        background-color: $transparent-color;\r\n        padding: 0;\r\n        margin-bottom: 0;\r\n        .breadcrumb-item{\r\n          font-family: $font-nunito;\r\n          &+.breadcrumb-item{\r\n            &:before{\r\n              content: $breadcrumb-content;\r\n            }\r\n          }\r\n          a{\r\n            svg{\r\n              width: $breadcrumb-size;\r\n              height: $breadcrumb-size;\r\n              vertical-align: $breadcrumb-svg-icon-align;\r\n            }\r\n          }\r\n        }\r\n      }\r\n\r\n    }\r\n  }\r\n}\r\n/*======= Page Header css ends  ======= */\r\n\r\n\r\n/**======Main Header css Start ======**/\r\n.offcanvas {\r\n  .page-wrapper {\r\n    .page-body-wrapper {\r\n      .page-body {\r\n        position: relative;\r\n        &:before {\r\n          position: fixed;\r\n          content: '';\r\n          background-color: rgba(0,0,0,0.5);\r\n          height: 100%;\r\n          width: 100%;\r\n          z-index: 10;\r\n          right: 0;\r\n          left: 255px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n.page-main-header{\r\n  max-width: 100vw;\r\n\r\n  .main-header-right{\r\n    width: 100%;\r\n    height: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    padding: $main-header-right-padding;\r\n    margin: 0;\r\n    svg{\r\n      color: $main-header-right-toggle-color;\r\n      line, polyline {\r\n        color: $main-header-right-toggle-color;\r\n      }\r\n    }\r\n    .nav-left{\r\n      i{\r\n        margin-right: 20px;\r\n      }\r\n      input:focus{\r\n        outline: 0 !important;\r\n      }\r\n    }\r\n    .nav-right{\r\n      text-align: right;\r\n      padding-left: $main-header-right-nav-right;\r\n      ul{\r\n        li{\r\n          .media{\r\n            .dotted-animation{\r\n              position: relative;\r\n              right: -3px;\r\n              top:-7px;\r\n              .animate-circle{\r\n                position: absolute;\r\n                top: -20px;\r\n                right: -4px;\r\n                height: 25px;\r\n                width: 25px;\r\n                z-index: 10;\r\n                border: 5px solid $primary-color;\r\n                border-radius: 70px;\r\n                animation: heartbit 1s ease-out;\r\n                animatio