0px;\r\n              width: 130px;\r\n              &:before,&:after{\r\n                left: inherit;\r\n                right: 10px;\r\n              }\r\n              li{\r\n                display: block;\r\n                a{\r\n                  font-size: 14px;\r\n                  color: $dark-color;\r\n                  i{\r\n                    margin-left: 10px;\r\n                    font-size: 13px;\r\n                    color: $dark-color;\r\n                  }\r\n                }\r\n              }\r\n            }\r\n          }\r\n        }\r\n        .flag-icon{\r\n          font-size: 16px;\r\n        }\r\n      }\r\n      .notification-dropdown{\r\n        top: 57px;\r\n        .txt-success {\r\n          svg {\r\n             polyline {\r\n               color: $success-color;\r\n             }\r\n          }\r\n        }\r\n        .txt-danger {\r\n          svg {\r\n            circle {\r\n              color: $danger-color;\r\n            }\r\n          }\r\n        }\r\n      }\r\n      .language-dropdown{\r\n        width: 160px;\r\n        text-align: left;\r\n        top: 57px;\r\n        li{\r\n          padding-top: 10px;\r\n          a{\r\n            color: $dark-color;\r\n            i{\r\n              margin-right: 10px;\r\n            }\r\n          }\r\n          &:first-child{\r\n            padding-top: 0;\r\n          }\r\n        }\r\n      }\r\n      .profile-dropdown{\r\n        right: -10px;\r\n        left: inherit;\r\n        width: 150px;\r\n        top: 63px;\r\n        &:before,&:after{\r\n          left: inherit;\r\n          right: 10px;\r\n        }\r\n        li{\r\n          display: block;\r\n          text-align: left;\r\n          padding-top: 10px;\r\n          &:nth-child(3){\r\n            padding-bottom: 10px;\r\n          }\r\n          &:nth-child(4){\r\n            border-top: 1px solid $light-semi-gray;\r\n          }\r\n          &:first-child{\r\n            padding-top: 0;\r\n          }\r\n          &:hover{\r\n            a{\r\n              color: $primary-color;\r\n              transition: $sidebar-transition;\r\n              svg{\r\n                color: $primary-color !important;\r\n                path, line, polyline {\r\n                  color: $primary-color !important;\r\n                }\r\n              }\r\n            }\r\n          }\r\n          a{\r\n            color: $theme-body-font-color;\r\n            transition: $sidebar-transition;\r\n            display: flex;\r\n            align-items: center;\r\n            svg{\r\n              margin-right: 10px;\r\n              color: $theme-body-font-color;\r\n              polyline {\r\n                color: $theme-body-font-color;\r\n              }\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n    li{\r\n      display: inline-block;\r\n      position: relative;\r\n\r\n    }\r\n  }\r\n}\r\n.nav-menus{\r\n  .notification-badge {\r\n    position: absolute;\r\n    right: 10px;\r\n    top: 1px;\r\n    padding: 4px 7px;\r\n  }\r\n  .onhover-dropdown{\r\n    cursor: pointer;\r\n    position: relative;\r\n    &:before{\r\n      display: none;\r\n    }\r\n    &:hover{\r\n      .onhover-show-div{\r\n        @extend  %for-animated-hover-box;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\nul.notification-dropdown.onhover-show-div{\r\n  width: 330px;\r\n  right: -18px;\r\n  left: initial;\r\n  &:before,&:after{\r\n    left: inherit !important;\r\n    right: 35px !important;\r\n  }\r\n  li{\r\n    display: block;\r\n    padding: 12px 20px;\r\n    border-bottom: 1px solid $light-color;\r\n    text-align: left;\r\n    h6{\r\n      small{\r\n        padding-top: 5px;\r\n        color: $dark-gray;\r\n        font-size: 12px;\r\n      }\r\n    }\r\n    span{\r\n      svg{\r\n        margin-top: 0 !important;\r\n        margin-right: 10px;\r\n        vertical-align: text-top;\r\n      }\r\n      .shopping-color{\r\n        path,line{\r\n          color: $primary-color;\r\n        }\r\n      }\r\n      .download-color{\r\n        path,line{\r\n          color: $success-color;\r\n        }\r\n      }\r\n      .alert-color{\r\n        path,line{\r\n          color: $danger-color;\r\n        }\r\n      }\r\n    }\r\n    p{\r\n      margin-left: 30px;\r\n    }\r\n    + li{\r\n      &:hover{\r\n        background-color: $light-color;\r\n      }\r\n    }\r\n  }\r\n}\r\n.onhover-show-div {\r\n  top: $header-size;\r\n  position: absolute;\r\n  z-index: 8;\r\n  background-color: $white;\r\n  transition: all linear 0.3s;\r\n  @extend  %for-animated-hover;\r\n  li{\r\n    a{\r\n      svg{\r\n        margin-top: 0 !important;\r\n        path, line{\r\n          color: $theme-body-font-color !important;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n/**======Main Header css Ends ======**/\r\n\r\n.nav-menus{\r\n  .search-form{\r\n    input {\r\n      border: 1px solid #eff0f1;\r\n      padding: 10px 10px 10px 70px;\r\n      border-radius: 50px;\r\n      background-color: $light-color;\r\n    }\r\n  }\r\n}\r\n\r\n/**=====================\r\n    Sidebar CSS Start\r\n==========================**/\r\n.page-wrapper{\r\n  .page-body-wrapper{\r\n    .sidebar{\r\n      height: calc(100vh - #{$header-size});\r\n      overflow: auto;\r\n      box-shadow: $sidebar-shadow;\r\n    }\r\n    .page-sidebar{\r\n      width: $sidebar-width;\r\n      position: $sidebar-position;\r\n      background: $sidebar-background-color;\r\n      top: 0;\r\n      height: calc(100vh);\r\n      z-index: $sidebar-z-index;\r\n      transition: $sidebar-transition;\r\n      .main-header-left{\r\n        display: inline-flex;\r\n        width: 100%;\r\n        height: $header-size;\r\n        padding: $main-header-padding;\r\n        align-items: center;\r\n        background-color: $main-header-left-bg-color;\r\n        z-index: $main-header-z-index;\r\n        box-shadow: -3px 1px 3px 1px rgba(68, 102, 242, 0.1);\r\n        .logo-wrapper{\r\n          padding-left: 10px;\r\n          height: 