\n            }\r\n          }\r\n          &.active {\r\n            > {\r\n              a {\r\n                > .fa-angle-right {\r\n                  &:before{\r\n                    content: $sidebar-open-icon;\r\n                    font-family: $sidebar-icon-font-family;\r\n                    transition: $sidebar-transition;\r\n                  }\r\n                }\r\n              }\r\n            }\r\n            > .sidebar-submenu {\r\n              display: block;\r\n              margin-left: 15px;\r\n              a {\r\n                i.pull-right {\r\n                  margin-top: 10px;\r\n                }\r\n              }\r\n            }\r\n          }\r\n        }\r\n        a {\r\n          text-decoration: none;\r\n        }\r\n        .sidebar-submenu {\r\n          display: none;\r\n          animation: 0.3s alternate sidebar-menu-open forwards;\r\n          list-style: none;\r\n          .sidebar-submenu {\r\n            padding-top: 0 !important;\r\n          }\r\n          > li {\r\n            > a {\r\n              padding-bottom: 7px;\r\n              padding-top: 7px;\r\n              font-size: 14px;\r\n              color: $dark-gray;\r\n              transition: $sidebar-transition;\r\n              text-transform: $sidebar-text-transform;\r\n              position: relative;\r\n              padding-left: 10px;\r\n              line-height: 2.5;\r\n              letter-spacing: 0.7px;\r\n              > {\r\n                .fa-circle {\r\n                  width: 12px;\r\n                  font-size: 4px;\r\n                  position: absolute;\r\n                  left: 0;\r\n                  top: 13px;\r\n                }\r\n                i {\r\n                  width: auto;\r\n                }\r\n                .fa-angle-down {\r\n                  width: auto;\r\n                }\r\n              }\r\n              &:hover {\r\n                color: $primary-color;\r\n                transition: $sidebar-transition;\r\n              }\r\n              &.active{\r\n                color: $primary-color;\r\n              }\r\n            }\r\n            &.active > a {\r\n              color: $theme-body-font-color;\r\n              &.active {\r\n                color: $primary-color;\r\n              }\r\n            }\r\n          }\r\n        }\r\n      }\r\n      ~ .page-body {\r\n        margin-left: $sidebar-width;\r\n        transition: $sidebar-transition;\r\n      }\r\n\r\n      &.open{\r\n        display: block;\r\n        margin-left: calc(-#{$sidebar-width + 5px});\r\n        ~ .page-body {\r\n          margin-left: 0;\r\n          transition: $sidebar-transition;\r\n        }\r\n        ~ footer {\r\n          margin-left: 0;\r\n          padding-right: 15px;\r\n        }\r\n        ~ .footer-fix {\r\n          width: calc(100% - 0px);\r\n        }\r\n      }\r\n    }\r\n    .sidebar-close{\r\n      .page-sidebar{\r\n        transition: $sidebar-transition;\r\n        transform: translate(-$sidebar-width);\r\n      }\r\n      .page-body{\r\n        transition: $sidebar-transition;\r\n        margin-left: 0 !important;\r\n      }\r\n    }\r\n\r\n  }\r\n}\r\n.right-sidebar {\r\n  top: 81px;\r\n  right: -285px;\r\n  height: 100%;\r\n  position: fixed;\r\n  width: 285px;\r\n  z-index: 9;\r\n  background-color: #fff;\r\n  transition: .5s;\r\n  box-shadow: 0 0 9px rgba(191, 191, 191, 0.36);\r\n  &.show{\r\n    right: 0;\r\n    transition: $sidebar-transition;\r\n  }\r\n  .modal-header{\r\n    .modal-title{\r\n      padding-top: 2px;\r\n    }\r\n  }\r\n  .friend-list-search {\r\n    position: relative;\r\n    background-color: $light-color ;\r\n    padding: 20px;\r\n    input {\r\n      color: $dark-gray;\r\n      width: 100%;\r\n      background-color: $white;\r\n      border: 1px solid $light-color ;\r\n      padding: 10px 15px;\r\n      border-radius: 25px;\r\n      letter-spacing: 1px;\r\n    }\r\n    .fa {\r\n      position: absolute;\r\n      right: 35px;\r\n      top: 34px;\r\n      font-size: 14px;\r\n      color: $light-gray ;\r\n    }\r\n  }\r\n  .chat-box{\r\n    .people-list{\r\n      ul {\r\n        padding-top: 20px;\r\n        li{\r\n          position: relative;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  svg{\r\n    width: 16px;\r\n    height: 16px;\r\n    margin-top: 5px;\r\n  }\r\n}\r\n\r\n/**=====================\r\n    Sidebar CSS Ends\r\n==========================**/\r\n\r\n\r\n/**=====================\r\n      Generic CSS Start\r\n==========================**/\r\n\r\n/*====== Padding css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .p-#{$i} {\r\n    padding:#{$i}px;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Padding css ends ======*/\r\n\r\n/*====== Padding-left css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .p-l-#{$i} {\r\n    padding-left:#{$i}px;\r\n  }\r\n  $i:$i+5;\r\n\r\n}\r\n/*====== Padding-left css ends ======*/\r\n\r\n/*====== Padding-top css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .p-t-#{$i} {\r\n    padding-top:#{$i}px !important;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Padding-top css ends ======*/\r\n\r\n\r\n/*====== Padding-bottom css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .p-b-#{$i} {\r\n    padding-bottom:#{$i}px !important;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Padding-bottom css ends ======*/\r\n\r\n/*====== Padding-right css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .p-r-#{$i} {\r\n    padding-right:#{$i}px;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Padding-right css ends ======*/\r\n\r\n\r\n/*====== Margin css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .m-#{$i} {\r\n    margin:#{$i}px !important;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Margin css ends ======*/\r\n\r\n/*====== Margin-top css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .m-t-#{$i} {\r\n    margin-top:#{$i}px !important;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Margin-top css ends ======*/\r\n\r\n/*====== Margin-Bottom css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .m-b-#{$i} {\r\n    margin-bottom:#{$i}px !important;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Margin-Bottom css ends ======*/\r\n\r\n/*====== Margin-left css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .m-l-#{$i} {\r\n    margin-left:#{$i}px !important;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Margin-left css ends ======*/\r\n\r\n/*====== Margin-right css starts ======*/\r\n$i:0;\r\n@while $i<=50 {\r\n  .m-r-#{$i} {\r\n    margin-right:#{$i}px;\r\n  }\r\n  $i:$i+5;\r\n}\r\n/*====== Margin-right css ends ======*/\r\n\r\n/*====== Border-radius css starts ======*/\r\n$i:0;\r\n@while $i<=10 {\r\n  .b-r-#{$i} {\r\n    border-radius:#{$i}px !important;\r\n  }\r\n  $i:$i+1;\r\n}\r\n/*====== Border-radius css ends ======*/\r\n\r\n\r\n/*====== Font-size css starts ======*/\r\n$i:12;\r\n@while $i<=100 {\r\n  .f-#{$i} {\r\n    font-size:#{$i}px;\r\n  }\r\n  $i:$i+2;\r\n}\r\n/*====== Font-size css ends ======*/\r\n\r\n\r\n/*====== Font-weight css starts ======*/\r\n$i:100,\r\n300,\r\n400,\r\n600,\r\n700,\r\n900;\r\n@each $val in $i {\r\n  .f-w-#{$val} {\r\n    font-weight: $val;\r\n  }\r\n}\r\n/*====== Font-weight css ends ======*/\r\n\r\n\r\n/*====== Font-style css starts ======*/\r\n$i:normal,\r\nitalic,\r\noblique,\r\ninitial,\r\ninherit;\r\n@each $val in $i {\r\n  .f-s-#{$val} {\r\n    font-style: $val;\r\n  }\r\n}\r\n/*====== Font-style css ends ======*/\r\n\r\n\r\n/*====== Text-Decoration css starts ======*/\r\n\r\n$i:overline,\r\nline-through,\r\nunderline,\r\ndashed,\r\nblink,\r\ndotted,\r\ninitial,\r\nnone,\r\nsolid,\r\nwavy,\r\ninherit,\r\ndouble;\r\n\r\n@each $val in $i {\r\n  .text-#{$val} {\r\n    text-decoration: $val;\r\n  }\r\n}\r\n\r\n/*====== Text-Decoration css ends ======*/\r\n\r\n\r\n/*====== Vertical-Align css starts ======*/\r\n$i:baseline,\r\nsub,\r\nsuper,\r\ntop,\r\ntext-top,\r\nmiddle,\r\nbottom,\r\ntext-bottom,\r\ninitial,\r\ninherit;\r\n@each $val in $i {\r\n  .#{$val} {\r\n    vertical-align: $val;\r\n  }\r\n}\r\n/*====== Vertical-Align css ends ======*/\r\n\r\n\r\n/*====== Position css starts ======*/\r\n\r\n$i:static,\r\nabsolute,\r\nfixed,\r\nrelative,\r\ninitial,\r\ninherit;\r\n@each $val in $i {\r\n  .p-#{$val} {\r\n    position: $val;\r\n  }\r\n}\r\n/*====== Position css ends ======*/\r\n\r\n/*====== Float css starts ======*/\r\n$i:left,\r\nright,\r\nnone;\r\n@each $val in $i {\r\n  .f-#{$val} {\r\n    float: $val;\r\n  }\r\n}\r\n/*====== Float css ends ======*/\r\n\r\n\r\n/*====== Overflow css starts ======*/\r\n$i:hidden,\r\nvisible,\r\nauto;\r\n@each $val in $i {\r\n  .o-#{$val} {\r\n    overflow: $val;\r\n  }\r\n}\r\n/*====== Overflow css ends ======*/\r\n\r\n\r\n/*====== Image-sizes css starts ======*/\r\n$i:10;\r\n@while $i<=100 {\r\n  .img-#{$i} {\r\n    width:#{$i}px !important;\r\n  }\r\n  $i:$i+10;\r\n}\r\n/*====== Image-sizes css ends ======*/\r\n\r\n/*======= Text css starts ===========*/\r\n$text-name:\r\n        primary,\r\n        secondary,\r\n        success,\r\n        danger,\r\n        info,\r\n        light,\r\n        dark,\r\n        warning;\r\n$text-color : $primary-color,\r\n$secondary-color,\r\n$success-color,\r\n$danger-color,\r\n$info-color,\r\n$light-color,\r\n$dark-color,\r\n$warning-color;\r\n@each $var in $text-name {\r\n  $i: index($text-name, $var);\r\n  .font-#{$var} {\r\n    color: nth($text-color, $i) !important;\r\n  }\r\n}\r\n/*======= Text css ends ===========*/\r\n\r\n\r\n/*======= Label-color css starts  ======= */\r\n\r\n.label {\r\n  border-radius: 2px;\r\n  color: $white;\r\n  font-size: 12px;\r\n  line-height: 1;\r\n  margin-bottom: 0;\r\n  text-transform: capitalize;\r\n}\r\n\r\n$label-name:theme,\r\nprimary,\r\nsecondary,\r\nsuccess,\r\ndanger,\r\ninfo,\r\nlight,\r\ndark,\r\nwarning;\r\n$label-color: $primary-color,\r\n$primary-color,\r\n$secondary-color,\r\n$success-color,\r\n$danger-color,\r\n$info-color,\r\n$light-color,\r\n$dark-color,\r\n$warning-color;\r\n@each $var in $label-name {\r\n  $i: index($label-name, $var);\r\n  .label-#{$var} {\r\n    background-color: nth($label-color, $i);\r\n  }\r\n}\r\n\r\n\r\n/*======= Label-color css ends  ======= */\r\n\r\n\r\n/*======= Badge-color css starts  ======= */\r\n\r\n$badge-name:\r\n        primary,\r\n        secondary,\r\n        success,\r\n        danger,\r\n        info,\r\n        light,\r\n        dark,\r\n        warning;\r\n$badge-color:\r\n        $primary-color,\r\n        $secondary-color,\r\n        $success-color,\r\n        $danger-color,\r\n        $info-color,\r\n        $light-color,\r\n        $dark-color,\r\n        $warning-color;\r\n@each $var in $badge-name {\r\n  $i: index($badge-name, $var);\r\n  .badge-#{$var} {\r\n    background-color: nth($badge-color, $i);\r\n  }\r\n}\r\n\r\n\r\n/*======= Badge-color css end  ======= */\r\n\r\n\r\n/*======= Background-color css starts  ======= */\r\n\r\n$background-name:\r\n        primary,\r\n        secondary,\r\n        success,\r\n        danger,\r\n        info,\r\n        light,\r\n        dark,\r\n        warning;\r\n$background-color:\r\n        $primary-color,\r\n        $secondary-color,\r\n        $success-color,\r\n        $danger-color,\r\n        $info-color,\r\n        $light-color,\r\n        $dark-color,\r\n        $warning-color;\r\n@each $var in $background-name {\r\n  $i: index($background-name, $var);\r\n  .bg-#{$var} {\r\n    background-color: nth($background-color, $i) !important;\r\n    color: $white;\r\n  }\r\n}\r\n\r\n\r\n/*======= Background-color css end  ======= */\r\n\r\n/*======= Font-color css starts  ======= */\r\n\r\n$text-name:primary,\r\nsecondary,\r\nsuccess,\r\ndanger,\r\ninfo,\r\nlight,\r\ndark,\r\nwarning,\r\ngoogle-plus,\r\ntwitter,\r\nlinkedin,\r\nfb;\r\n$label-color:$primary-color,\r\n$secondary-color,\r\n$success-color,\r\n$danger-color,\r\n$info-color,\r\n$light-color,\r\n$dark-color,\r\n$warning-color;\r\n/*======= Font-color css end  ======= */\r\n\r\n\r\n/*======= Button-color css starts  ======= */\r\n// btn--color//\r\n@each $btn-name, $btn-color in  (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .btn-#{$btn-name}{\r\n    background-color: $btn-color !important;\r\n    border-color: $btn-color !important;\r\n    &.disabled{\r\n      background-color: $btn-color !important;\r\n      border-color: $btn-color !important;\r\n    }\r\n    &:disabled{\r\n      background-color: $btn-color !important;\r\n      border-color: $btn-color !important;\r\n    }\r\n    &:hover,&:focus,&:active,&.active{\r\n      background-color: darken( $btn-color, 10% ) !important;\r\n      border-color: darken( $btn-color, 10% ) !important;\r\n    }\r\n    &:focus{\r\n      box-shadow: 0 0 0 0.2rem lighten( $btn-color, 30% );\r\n    }\r\n  }\r\n}\r\n/*======= Button-color css ends  ======= */\r\n@each $btn-name, $btn-color in  (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .btn-outline-#{$btn-name}-2x{\r\n    border-width: 2px;\r\n    border-color:$btn-color;\r\n    color: $btn-color;\r\n    background-color: transparent;\r\n    &:hover,&:focus,&:active,&.active{\r\n      color: white;\r\n      background-color: darken( $btn-color, 10%) !important;\r\n      border-color: darken( $btn-color, 10% )!important;\r\n      box-shadow: none;\r\n    }\r\n  }\r\n}\r\n\r\n// outline buttons//\r\n@each $btn-name, $btn-color in  (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .btn-outline-#{$btn-name}{\r\n    border-color:$btn-color;\r\n    color: $btn-color;\r\n    background-color: transparent;\r\n    &.disabled{\r\n      color: $btn-color;\r\n    }\r\n    @if($btn-name == \"light\"){\r\n      color: $dark-color;\r\n    }\r\n    &:hover,&:focus,&:active,&.active{\r\n      color: white;\r\n      background-color: darken( $btn-color, 10%) !important;\r\n      border-color: darken( $btn-color, 10% ) !important;\r\n    }\r\n  }\r\n}\r\n\r\n//active button css //\r\n\r\n@each $btn-name, $btn-color in  (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n  .btn-#{$btn-name}:not([disabled]):not(.disabled).active{\r\n    background-color:darken($btn-color,10%);\r\n    border-color:darken($btn-color,10%);\r\n    box-shadow: none !important;\r\n    &:hover,&:focus,&:active,&.active{\r\n      color: white;\r\n      background-color: darken( $btn-color, 10% );\r\n      border-color: darken( $btn-color, 10% );\r\n    }\r\n  }\r\n}\r\n\r\n//* active button css end *//\r\n\r\n@each $btn-name, $btn-color in  (primary, $primary-color),\r\n        (secondary, $secondary-color) ,\r\n        (success, $success-color),\r\n        (danger, $danger-color),\r\n        (info, $info-color),\r\n        (light, $light-color),\r\n        (dark, $dark-color),\r\n        (warning, $warning-color) {\r\n\r\n  .btn-outline-#{$btn-name}-2x:not([disabled]):not(.disabled).active{\r\n    background-color:$btn-color;\r\n    border-color:$btn-color;\r\n    box-shadow: none !important;\r\n    color: white;\r\n    &:hover,&:focus,&:active,&.active{\r\n      color: white;\r\n      box-shadow: none !important;\r\n      background-color: darken( $btn-color, 10% ) !important;\r\n      border-color: darken( $btn-color, 10% ) !important;\r\n    }\r\n  }\r\n}\r\n\r\n/*======= Table-Border-Bottom-color css starts  ======= */\r\n\r\n$border-name:primary,\r\nsecondary,\r\nsuccess,\r\ndanger,\r\ninfo,\r\nlight,\r\ndark,\r\nwarning;\r\n$border-color:$primary-color,\r\n$secondary-color,\r\n$success-color,\r\n$danger-color,\r\n$info-color,\r\n$light-color,\r\n$dark-color,\r\n$warning-color;\r\n@each $var in $border-name {\r\n  $i: index($border-name, $var);\r\n  table thead .border-bottom-#{$var} th,\r\n  table tbody .border-bottom-#{$var} th,\r\n  table tbody .border-bottom-#{$var} td {\r\n    border-bottom: 1px solid nth($border-color, $i);\r\n  }\r\n}\r\n\r\n\r\n/*======= Table-Border-Bottom-color css ends  ======= */\r\n\r\n\r\n/*======= Table styling css starts  ======= */\r\n\r\n$table-name:\r\n        primary,\r\n        secondary,\r\n        success,\r\n        danger,\r\n        info,\r\n        light,\r\n        dark,\r\n        warning;\r\n$table-color:$primary-color,\r\n$secondary-color,\r\n$success-color,\r\n$danger-color,\r\n$info-color,\r\n$light-color,\r\n$dark-color,\r\n$warning-color;\r\n@each $var in $table-name {\r\n  $i: index($table-name, $var);\r\n  .table-styling .table-#{$var},\r\n  .table-styling.table-#{$var} {\r\n    background-color: nth($border-color, $i);\r\n    color: $white;\r\n    border: 3px solid nth($border-color, $i);\r\n    thead {\r\n      background-color: darken(nth($border-color, $i), 10%);\r\n      border: 3px solid darken(nth($border-color, $i), 10%);\r\n    }\r\n  }\r\n}\r\n\r\n\r\n/*======= Table styling css ends  ======= */\r\n\r\n\r\n\r\n\r\n/*======= All-Borders-color css starts  ======= */\r\n\r\n$b-name:primary,\r\nsecondary,\r\nsuccess,\r\ndanger,\r\ninfo,\r\nlight,\r\ndark,\r\nwarning;\r\n$b-color:$primary-color,\r\n$secondary-color,\r\n$success-color,\r\n$danger-color,\r\n$info-color,\r\n$light-color,\r\n$dark-color,\r\n$warning-color;\r\n@each $var in $b-name {\r\n  $i: index($b-name, $var);\r\n  .b-#{$var} {\r\n    border: 1px solid nth($b-color, $i) !important;\r\n  }\r\n  .b-t-#{$var} {\r\n    border-top: 1px solid nth($b-color, $i) !important;\r\n  }\r\n  .b-b-#{$var} {\r\n    border-bottom: 1px solid nth($b-color, $i) !important;\r\n  }\r\n  .b-l-#{$var} {\r\n    border-left: 1px solid nth($b-color, $i) !important;\r\n  }\r\n  .b-r-#{$var} {\r\n    border-right: 1px solid nth($b-color, $i) !important;\r\n  }\r\n}\r\n\r\n\r\n/*======= All-Borders-color css ends  ======= */\r\n\r\n\r\n/*====== Border width css starts ======*/\r\n$i:1;\r\n@while $i<=10 {\r\n  .border-#{$i} {\r\n    border-width:#{$i}px !important;\r\n  }\r\n  $i:$i+1;\r\n}\r\n/*====== Border width css ends ======*/\r\n\r\n.opacity-0{\r\n  opacity: 0;\r\n}\r\n.shadow-0{\r\n  box-shadow: none;\r\n}\r\n\r\n/**====== custom scrollbar css start ======**/\r\n.digits{\r\n  font-family:$font-work-sans, $font-serif;\r\n}\r\n.custom-scrollbar {\r\n  &::-webkit-scrollbar-track {\r\n    -webkit-box-shadow: inset 0 0 6px $light-gray;\r\n  }\r\n  &::-webkit-scrollbar {\r\n    width: 6px;\r\n  }\r\n  &::-webkit-scrollbar-thumb {\r\n    background-color: rgba($primary-color, 0.10);\r\n  }\r\n}\r\n\r\n/**====== Custom scrollbar css end ======**/\r\n\r\n/**====== Animation css Start ======**/\r\n.line{\r\n  pre{\r\n    font-size: 100%;\r\n  }\r\n}\r\n/**====== Animation css end ======**/\r\n\r\n/**====== list style css Start ======**/\r\n.list-circle{\r\n  list-style: circle;\r\n}\r\n/**====== list style css end ======**/\r\n/**=====================\r\n      Generic CSS Ends\r\n==========================**/\r\n\r\n\r\n/**=====================\r\n     Chat CSS Start\r\n==========================**/\r\n.chat-box {\r\n  .toogle-bar{\r\n    display: none;\r\n  }\r\n  .people-list {\r\n    .search{\r\n      position: relative;\r\n      .form-control{\r\n        background-color: $light-body-bg-color;\r\n        border: 1px solid $light-color;\r\n        &::placeholder{\r\n          color: $semi-dark;\r\n        }\r\n      }\r\n      i {\r\n        position: absolute;\r\n        r